"use client";

interface Product {
    id: string;
    name: string;
    sku: string;
    price: number;
}

export default function ProductList({ products }: { products: Product[] }) {
    return (
        <div className="bg-white border rounded-lg p-4 h-[calc(100vh-180px)] overflow-y-auto">
            <h2 className="text-sm font-semibold mb-3">
                Existing Products
            </h2>

            {products.length === 0 ? (
                <p className="text-sm text-gray-400">
                    No products found
                </p>
            ) : (
                <ul className="space-y-2">
                    {products.map((p) => (
                        <li
                            key={p.id}
                            className="border rounded-md px-3 py-2 hover:bg-gray-50"
                        >
                            <div className="font-medium text-sm">
                                {p.name}
                            </div>
                            <div className="text-xs text-gray-500">
                                SKU: {p.sku}
                            </div>
                            <div className="text-xs text-gray-500">
                                K{p.price.toFixed(2)}
                            </div>
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
}
